.img-thumbnail {
	width: 230px;
	height: 230px;
	margin: 20px;
	-webkit-transition: .2s;
	-o-transition: .2s;
	transition: .2s;
}

.img-thumbnail:hover{
   -webkit-transform: scale(1.1);
       -ms-transform: scale(1.1);
           transform: scale(1.1);
   cursor: pointer;
}


